import React from 'react'
import './Registration.css'
import 'bootstrap/dist/css/bootstrap.min.css'
export default function Registration() {
    return (
        <>
            <div id="regform" className="container">
                <h2 className="py-3 text-center">Registration Form</h2>
            <div className="row py-2">
                <div className="col-md-12">
                    <div className="form-group">
                        <input placeholder="First Name" type="text" className="form-control"/>
                    </div>

                    <div className="form-group">
                        <input placeholder="Last Name" type="text" className="form-control"/>
                    </div>

                    <div className="form-group">
                        <input placeholder="Mobile" type="text" className="form-control"/>
                    </div>

                    <div className="form-group">
                        <input placeholder="exmaple@donesol.co.uk" type="email" className="form-control"/>
                        <small className="form-text text-muted">  Email domain must be @donesol.co.uk</small>
                    </div>

                    <div className="form-group">
                        <input placeholder="Password" type="password" className="form-control"/>
                    </div>

                    <div className="form-group">
                        <select className="custom-select">
                            <option>Gender</option>
                            <option>Male</option>
                            <option>Female</option>
                            <option>Other</option>
                        </select>
                       
                    </div>
                    <div className="form-group">
                        <button className="form-control btn-primary">Register</button>
                    </div>

                </div>

            </div>

            </div>
        </>
    )
}
