import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './Login.css'
export default function Login() {
    return (
        <div>
            <div id="lgform" className="container">
                <h2 className="py-3 text-center">Sign in Form</h2>
            <div className="row py-2">
                <div className="col-md-12">
                    
                    <div className="form-group">
                        <input placeholder="exmaple@donesol.co.uk" type="email" className="form-control"/>
                    </div>

                    <div className="form-group">
                        <input placeholder="Password" type="password" className="form-control"/>
                    </div>
                    <div className="form-group">
                        <button className="form-control btn-primary">Sign in</button>
                    </div>

                </div>

            </div>

            </div>
        </div>
    )
}
